@keyframes showAndHide {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

#welcome {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  height: 100%;
  background-color: #000;

  video {
    padding: 0;
    margin: 0;
    animation-name: showAndHide;
    animation-duration: 4.5s;
    animation-delay: 0s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }
}